.navigator-container {
    margin: 0px;
}

.game-card-set-container{
    width: 100%;
    height: 80vh;
    overflow: auto;
}

.data-filter-container {
    display: flex;
    align-items: center;
    margin: 10px 40px;
    gap: 20px;
}

.title-container{
    display: flex;
    flex-direction: column;
    font-size: 32px;
    margin-top: 20px
}

.title {
    margin-left: 40px;
    color: white;

}

.home {
    display: flex;
    width: 100%;
    height: 100%;
}

.game-card-set-container::-webkit-scrollbar {
    width: 10px;
    margin-right: 10px;
}

.game-card-set-container::-webkit-scrollbar-thumb {
    background-color: #444;
}

.game-card-set-container::-webkit-scrollbar-track {
    background-color: transparent;
}

collections {
    width: 100%;
}